
.order {
    padding: 50px 0;
    background-image: url(../img/order/order_bg.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;


    &__title {
        text-align: center;
        color: #fff;
        line-height: 1.1;
        br {
            display: none;
        }
        @media (max-width: 450px) {
            text-align: left;
            span {
                display: none;
            }
            br {
                display: block;
            }
        }
    }
    &__sub-title {
        font-family: 'Montserrat', sans-serif;
        text-align: center;
        color: #fff;
        margin-top: 10px;
        font-weight: 300;
        @media (max-width: 450px) {
            text-align: left;
        }
    }
    &__form {
        width: 100%;
    }
    &__form-btn {
        @media (max-width: 950px) {
            margin-top: 20px;
        }
    }
}
.form {

    &__wrp {
        display: flex;
        justify-content: center;
        width: 100%;
        margin: 0 auto;
        column-gap: 60px;
        margin-top: 60px;
        @media (max-width: 1100px) {
            column-gap: 15px;
        }
        @media (max-width: 950px) {
            display: block;
            margin: 0 auto;
            width: 500px;
            margin-top: 20px;
        }
        @media (max-width: 550px) {
            width: 100%;
        }
    }
    &__btn-wrp {
        flex: 0 1 460px;
    }
}
.form__btn-text {
    color: #fff;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    font-size: 14px;
}

.form-name, .form-phone, .form-btn{
    height: 60px;
    flex: 0 1 460px;
}
.form-name, .form-phone {

    font-size: 20px;
    padding: 10px 30px;
    &::-webkit-input-placeholder {
        font-family: 'Montserrat', sans-serif;
        font-size: 16px;
    }
    &::-webkit-input-placeholder {
        font-family: 'Montserrat', sans-serif;
        font-size: 16px;
    }
    &::-moz-placeholder{
        font-family: 'Montserrat', sans-serif;
        font-size: 16px;
    }/* Firefox 19+ */
    &:-moz-placeholder{
        font-family: 'Montserrat', sans-serif;
        font-size: 16px;
    }/* Firefox 18- */
    &:-ms-input-placeholder{
        font-family: 'Montserrat', sans-serif;
        font-size: 16px;
    }
}

.form-phone {
}
.form-btn {
    font-family: 'Montserrat', sans-serif;
    background-color: #102F28;
    color: #fff;
    font-size: 16px;
    padding: 7px 10px;
    transition: all .3s;
    width: 100%;
    &:hover {
        background-color: #0c231e;
    }
}
.form-btn-text {
    font-family: 'Montserrat', sans-serif;
    display: block;
    margin-top: 10px;
    color: #fff;
}
.form-order-name, .form-order-phone{
    @media (max-width: 950px) {
        width: 100%;
    }
}
.form-order-phone {
    @media (max-width: 950px) {
        margin-top: 10px;
    }

}
