
.map {
    position: relative;
    z-index: 1;
    padding: 50px 0;
    @media (max-width: 870px) {
        padding-top: 0px;
    }

    .dark-color {
        &.active {
            color: #fff;
        }
    }

    &__content-wrp {
        display: flex;
        justify-content: space-between;

        @media (max-width: 870px) {
            flex-direction: column;
            align-items: center;
            justify-content: start;
        }
    }
    &__left {
        span {
            display: block;
        }
    }
    &__title {
        margin-top: 44px;
        max-width: 430px;
        line-height: 1.1;
    }
    .map__sub-title {
        color: #2D2D2D;
        font-weight: 500;
        margin-top: 10px;
        margin-bottom: 40px;
        @media (max-width: 870px) {
            margin-bottom: 20px;
        }
    }
    &__connect-item {
        span {
            color: #000;
        }
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    .map__connect-item-top {
        font-weight: 500;
        br {
            display: none;
            @media (max-width: 375px) {
                display: block;
            }
        }
    }
    &__connect-item-bottom {
        font-family: 'Montserrat', sans-serif;
        font-size: 16px;
        margin-top: 7px;
        a {
            color: inherit;
            margin-top: 7px;
            display: block;
        }
    }
    &__btn {
        margin-top: 40px;
        width: 260px;
        height: 64px;
        padding: 5px 10px;
        background-color: #2D2D2D;
        color: #fff;
        font-size: 14px;
        font-weight: 700;
        font-family: 'Montserrat', sans-serif;
        transition: all .3s;
        &:hover {
            background-color: #000;
        }
    }
    &__btn-text {
        font-family: 'Montserrat', sans-serif;
        font-size: 16px;
        margin-top: 13px;
        color: #2d2d2d7d;
        .dark-color {
            &.active {
                color: #fff;
            }
        }
    }
    .map__slogan {
        font-family: 'Monotype Corsiva';
        margin-top: 67px;
        color: #2d2d2d7d;
        .dark-color {
            &.active {
                color: #fff;

            }
        }
        @media (max-width: 870px) {
            margin-top: 15px;
        }
    }
    &__right {
        width: 681px;
        height: 681px;
        @media (max-width: 1300px) {
            max-width: 50%;
        }
        @media (max-width: 870px) {
            max-width: 526px;
            height: 500px;
        }
        @media (max-width: 545px) {
            max-width: 100%;
        }
        @media (max-width: 445px) {
            height: 320px;
        }
    }
    &__wrp {
        width: 100%;
        height: 100%;
    }
    .map-dekst {
        @media (max-width: 870px) {
            display: none;
        }
    }
    .map-mobile {
        display: none;
        margin-top: 20px;
        @media (max-width: 870px) {
            display: block;
        }
        @media (max-width: 375px) {
            margin-right: auto;
        }
    }
}

