
.header {
    width: 82.35%;
    padding-top: 36px;
    display: flex;
    justify-content: space-between;
    margin-left: auto;
    align-items: center;
    position: relative;
    z-index: 1;
    @media (max-width: 1300px) {
        margin-left: 0;
        width: 100%;
    }
    @media (max-width: 1115px) {
        width: 100%;
        padding-left: 15px;
        margin-left: 0;
    }
    &__menu {
        position: absolute;
        width: 202px;
        height: 385px;
        background-color: #102F28;
        display: flex;
        justify-content: center;
        padding: 54px 15px;
        top: 0;
        right: 0;
        z-index: 2;
        transform: translateY(-100%);
        transition: all .5s;

        li {
            &:not(:last-child) {
                margin-bottom: 47px;
            }
            a {
                font-family: 'Montserrat', sans-serif;
                color: #fff;
                font-size: 16px;
                transition: all .3s;
                &:hover {
                    color: rgb(181, 181, 181);
                }
            }
        }
    }
    &__logo-wrp {
        width: 15%;
        height: 5%;
        @media (max-width: 1350px) {
            height: auto;
            width: auto;
        }
    }
    &__logo {
        @media (max-width: 1350px) {
            height: 40px;
            width: 140px;
        }
    }
    &__rigth-wrp {
        display: flex;
        align-items: center;
        column-gap: 153px;
        @media (max-width: 1800px) {
            column-gap: 0px;
        }
    }
    &__tel {
        a {
            font-family: 'Montserrat', sans-serif;
            margin-right: 15px;
            font-size: 16px;
            color: #2D2D2D;
            &.active {
                color: #fff;
            }
            @media (max-width: 420px) {
                margin-right: 0;
            }
        }
    }
    &__button {
        font-family: 'Montserrat', sans-serif;
        font-size: 14px;
        font-weight: 700;
        width: 260px;
        height: 64px;
        background-color: transparent;
        color: #102F28;
        border: #102F28 solid 1px;
        margin-right: 20px;
        cursor: pointer;
        padding: 5px 7px;
        transition: transform .3s;
        &.active {
            color: #fff;
            border: #fff solid 1px;
        }
        @media (max-width: 1500px) {
            width: 221px;
            height: 50px;
        }
        &:hover {
            transform: translateY(-2px);
        }
        @media (max-width: 420px) {
            margin-right: 0;
            margin-bottom: 15px;
        }
    }
    &__icons {
        display: flex;
        align-items: center;
        margin-right: 40px;
        @media (max-width: 1500px) {
            margin-right: 20px;
        }
    }
    &__switch {
        margin-right: 13px;
        cursor: pointer;
        transition: all .3s;
        &:hover {
            transform: translateY(-2px);
        }
        &.active {
            circle {
                fill: #fff;
            }
            path {
                fill: #2D2D2D;
            }
        }
    }
    &__burger {
        cursor: pointer;
        transition: all .3s;
        display: flex;
        align-items: center;
        justify-content: center;
        @media (max-width: 1400px) {
            width: 30px;
            height: 30px;
            path {
                display: block;
            }
        }
        &:hover {
            transform: translateY(-2px);
        }
        &.active {
            path {
                fill: #fff;
            }
        }
    }
}

.header-tel-dekst {
    @media (max-width: 1127px) {
        display: none;
    }
}
.header-tel-mobile {
    display: none;
    margin-top: 20px;
    position: relative;
    z-index: 1;
    @media (max-width: 1127px) {
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }
    @media (max-width: 700px) {
        margin-top: 26px;
        justify-content: center;
    }
    @media (max-width: 420px) {
        flex-direction: column-reverse;
        margin-top: 55px;
    }
    @media (max-width: 400px) {
        margin-top: 78px;
    }
}

.header-dekst {
    @media (max-width: 700px) {
        display: none;
    }
}
.header-mobile {
    display: none;
    @media (max-width: 700px) {
        display: flex;
        padding: 15px 0;
        padding-left: 15px;
        z-index: 3;
    }
}



