@font-face {
    font-family: 'SovMod';
    src: url('../fonts/SovMod.eot');
    src: url('../fonts/SovMod.eot?#iefix') format('embedded-opentype'),
        url('../fonts/SovMod.woff2') format('woff2'),
        url('../fonts/SovMod.woff') format('woff'),
        url('../fonts/SovMod.ttf') format('truetype'),
        url('../fonts/SovMod.svg#SovMod') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}
@font-face {
    font-family: 'Monotype Corsiva';
    src: url('../fonts/MonotypeCorsiva.eot');
    src: url('../fonts/MonotypeCorsiva.eot?#iefix') format('embedded-opentype'),
        url('../fonts/MonotypeCorsiva.woff2') format('woff2'),
        url('../fonts/MonotypeCorsiva.woff') format('woff'),
        url('../fonts/MonotypeCorsiva.svg#MonotypeCorsiva') format('svg');
    font-weight: normal;
    font-style: italic;
    font-display: swap;
}
// font-family: 'Akaya Kanadaka', cursive;

body {
    font-family: 'SovMod', sans-serif;
    color: #2D2D2D;
}
.wrp {
    background-color: #fff;
    overflow: hidden;
    //position: relative;
}

.container {
    max-width: 1515px;
    margin: 0 auto;
    padding: 0 15px;
}
a {
    color: #2D2D2D;
}
.title {
    text-transform: uppercase;
    font-size: calc(26px + 14 * (100vw / 1485));
    @media (max-width: 767px) {
        font-size: calc(25px + (14 + 14 * 0.7) * ((100vw - 320px) / 1485));
    }
}

.sub-title {
    font-size: calc(18px + 7 * (100vw / 1485));
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    @media (max-width: 767px) {
        font-size: calc(18px + (7 + 7 * 0.7) * ((100vw - 320px) / 1485));
    }
}

.dark-img {
    display: none;
    &.active {
        display: block;
    }
}
.white-img {
    &.active {
        display: none;
    }
}






