

.theses {
    padding: 100px 50px;
    font-family: 'Montserrat', sans-serif;
    position: relative;
    overflow: hidden;
    @media (max-width: 807px) {
        padding: 100px 0;
    }
    @media (max-width: 550px) {
        padding-top: 0;
        padding-bottom: 30px;
    }

    &__content-wrp {
        display: flex;
        justify-content: space-between;
        position: relative;
        z-index: 1;
        @media (max-width: 550px) {
            display: block;
        }
    }
    &__top {
    }
    &__top-list {
        margin-bottom: 32px;
        position: relative;
        @media (max-width: 550px) {
            margin-bottom: 18px;
        }
        &::after {
            content: '';
            position: absolute;
            width: 10px;
            height: 10px;
            background-color: #102F28;
            border-radius: 50%;
            left: 0;
            top: 38px;
            @media (max-width: 550px) {
                top: 23px;
            }
        }
        @media (max-width: 940px) {
            font-size: 18px;
        }
        @media (max-width: 645px) {
            font-size: 16px;
        }
        &::before {
            @media (max-width: 550px) {
                content: '';
                position: absolute;
                width: 108%;
                height: 1px;
                left: -16px;
                top: 28px;
                background-color: #2D2D2D;
            }
        }
    }
    &__bottom-list {
        font-size: 16px;
        @media (max-width: 645px) {
            font-size: 14px;
        }
        @media (max-width: 550px) {
            display: inline-block;
            margin-bottom: 30px;
        }
    }
    .dark-color {
        &.active {
            color: #fff;
        }
    }

    &__bg {
        position: absolute;
        max-width: 368px;
        top: -300px;
;
    left: 0;
    }
}
.line {
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #2D2D2D;
    top: 43px;
    left: 0;
    @media (max-width: 550px) {
        display: none;
    }
}

