
.footer {
    padding: 56px 0 50px 0;
    background-color: #2D2D2D;

    &.active {
        background-color: #000;
    }


    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: 1020px) {
            flex-direction: column;

        }
    }
    &__logo {
        margin-right: 15px;
    }
    &__menu {

        ul {
            display: flex;
            column-gap: 70px;
            margin-right: 15px;
            @media (max-width: 1400px) {
                column-gap: 50px;
            }
            @media (max-width: 1300px) {
                column-gap: 20px;
            }
            @media (max-width: 1020px) {
                margin-top: 50px;
                margin-right: 0;
            }
            @media (max-width: 560px) {
                flex-direction: column;
                row-gap: 47px;
            }
        }
        li {
            a {
                font-family: 'Montserrat', sans-serif;
                color: #fff;
                font-size: 16px;
                transition: all .3s;
                &:hover {
                    color: rgb(181, 181, 181);
                }
            }
        }
    }
    &__tel {
        display: flex;
        align-items: center;
        a {
            color: #fff;
            font-family: 'Montserrat', sans-serif;
            font-size: 16px;
            display: block;
            margin-right: 15px;
            transition: all .3s;
            &:hover {
                color: rgb(181, 181, 181);
            }
            @media (max-width: 1185px) {
                margin-right: 0;
                margin-top: 10px;
            }
        }
        @media (max-width: 1185px) {
            flex-direction: column-reverse;
            align-items: center;
        }
    }
    &__btn {
        width: 260px;
        height: 64px;
        padding: 5px 10px;
        border: 1px solid #fff;
        background-color: transparent;
        color: #fff;
        font-family: 'Montserrat', sans-serif;
        font-weight: 700;
        font-size: 14px;
        transition: all .3s;
        &:hover {
            color: #2D2D2D;
            background-color: #fff;
        }
        @media (max-width: 1185px) {
            height: 54px;
        }
        @media (max-width: 1020px) {
            margin-top: 30px;
        }
    }
}