

.main {
    width: 100%;
    padding-bottom: 50px;
    background-repeat: no-repeat;
    background-position: 50% 0;
    overflow: hidden;
    &__content-wrp {
        display: flex;
        height: 100%;
    }
    &__content-left {
        width: 100%;
        position: relative;
        z-index: 2;
        @media (max-width: 1300px) {
            padding-left: 15px;
        }
        @media (max-width: 700px) {
            padding-left: 0;
        }
    }
    &__img-text-wrp {
        display: flex;
    }
    &__title-btns-wrp {
        margin-top: 124px;
        padding-right: 15px;
        @media (max-width: 1500px) {
            margin-top: 52px;
        }
        @media (max-width: 700px) {
            margin: 0 auto;
            margin-top: 50px;
            background-color: #00000083;
            padding: 69px 15px;
        }

    }
    &__img-title {
        width: 100%;
        @media (max-width: 1600px) {
            width: 80%;
        }
    }
    &__title-h1 {
        color: #fff;
        //text-shadow: #2D2D2D 2px 0px, #2D2D2D 2px 2px, #2D2D2D 0px 2px, #2D2D2D -2px 2px, #2D2D2D -2px 0px, #2D2D2D -2px -2px, #2D2D2D 0px -2px, #2D2D2D 2px -2px;
        text-shadow:0px 1px 0 rgb(45,45,45),0px -1px 0 rgb(45,45,45),1px 0px 0 rgb(45,45,45),-1px 0px 0 rgb(45,45,45),1px 1px 0 rgb(45,45,45),1px -1px 0 rgb(45,45,45),-1px 1px 0 rgb(45,45,45),-1px -1px 0 rgb(45,45,45),0px 2px 0 rgb(45,45,45),0px -2px 0 rgb(45,45,45),2px 0px 0 rgb(45,45,45),-2px 0px 0 rgb(45,45,45),2px 2px 0 rgb(45,45,45),2px -2px 0 rgb(45,45,45),-2px 2px 0 rgb(45,45,45),-2px -2px 0 rgb(45,45,45);
        letter-spacing: 11px;
        font-size: calc(30px + 38 * (100vw / 1920));
        &.active {
            color: #2D2D2D;
            text-shadow:0px 1px 0 #fff,0px -1px 0 #fff,1px 0px 0 #fff,-1px 0px 0 #fff,1px 1px 0 #fff,1px -1px 0 #fff,-1px 1px 0 #fff,-1px -1px 0 #fff,0px 2px 0 #fff,0px -2px 0 #fff,2px 0px 0 #fff,-2px 0px 0 #fff,2px 2px 0 #fff,2px -2px 0 #fff,-2px 2px 0 #fff,-2px -2px 0 #fff;
        }
        @media (max-width: 980px) {
            font-size: 40px;
        }
        @media (max-width: 720px) {
            font-size: 35px;
        }
        @media (max-width: 700px) {
            color: #2D2D2D;
            text-shadow:0px 1px 0 #fff,0px -1px 0 #fff,1px 0px 0 #fff,-1px 0px 0 #fff,1px 1px 0 #fff,1px -1px 0 #fff,-1px 1px 0 #fff,-1px -1px 0 #fff,0px 2px 0 #fff,0px -2px 0 #fff,2px 0px 0 #fff,-2px 0px 0 #fff,2px 2px 0 #fff,2px -2px 0 #fff,-2px 2px 0 #fff,-2px -2px 0 #fff;
        }
        @media (max-width: 400px) {
            font-size: 25px;
        }
    }
    &__title, &__title2 {
        font-size: 60px;
        font-size: calc(26px + 34 * (100vw / 1920));
        &.active {
            color: #fff;
        }
        @media (max-width: 980px) {
            font-size: 35px;
        }
        @media (max-width: 720px) {
            font-size: 30px;
        }
        @media (max-width: 700px) {
            color: #fff;
        }
        // @media (max-width: 767px) {
        //     font-size: calc(26px + (34 + 34 * 0.7) * ((100vw - 320px) / 1920));
        // }
        @media (max-width: 1600px) {
            margin-top: 10px;
        }
        @media (max-width: 400px) {
            font-size: 20px;
        }
    }
    &__title {
        margin-top: 7px;
        padding-left: 151px;
        @media (max-width: 1600px) {
            padding-left: 80px;
        }
    }
    &__title2 {
        margin-top: 7px;
        padding-left: 50px;
        @media (max-width: 1600px) {
            padding-left: 0px;
        }
        @media (max-width: 400px) {
            padding-left: 47px;
        }
    }
    &__btns-wrp {
        margin-top: 25px;
        margin-left: 52px;
        @media (max-width: 1600px) {
            margin-left: 0;
        }
    }
    &__btn-size, &__btn-price {
        font-family: 'Montserrat', sans-serif;
        font-weight: 700;
        font-size: 16px;
        width: 260px;
        height: 54px;
        display: block;
        transition: all .3s;
        &:not(:last-child) {
            margin-bottom: 10px;
        }
        @media (max-width: 700px) {
            width: 100%;
            margin: 0 auto;
        }
    }
    &__btn-size {
        color: #fff;
        background-color: #2D2D2D;
        &:hover {
            background-color: #000000;
        }
    }
    &__btn-price {
        background-color: #C73D42;
        color: #fff;
        &:hover {
            background-color: #a23437;
        }
    }
    &__content-right {
        width: 61.3%;
        margin-left: auto;
        position: relative;
        @media (max-width: 700px) {
            position: absolute;
            width: 100%;
        }
    }
    &__img-right {
        width: 100%;
        height: 100%;
        position: relative;
        z-index: 1;
        @media (max-width: 1300px) {
            min-width: 459px;
            //height: 500px;
        }
        @media (max-width: 700px) {
            min-width: 100%;
            height: 545px;
        }
    }
    &__right-bg {
        position: absolute;
        top: 69px;
        right: 528px;
        width: 70%;
        //transform: translateX(-528px);
        @media (max-width: 1920px) {
            right: calc(200px + (528 - 200) * ((100vw - 1000px) / (1920 - 1000)));
        }
        @media (max-width: 700px) {
            display: none;
        }
        @media (max-width: 500px) {
            width: 0;
        }
    }

    &__img-left-wrp {
        width: 33.8%;
        margin-right: 70px;
        position: relative;
        z-index: 1;
        @media (min-width: 1500px) {
            margin-right: 70px;
        }
        @media (max-width: 1300px) {
            display: none;
        }
    }
    &__img-left {
        width: 100%;
        margin-top: 47px;
        @media (max-width: 1300px) {
            width: 232px;
        }
    }
    &__content-bottom {
        width: 82.35%;
        margin-top: 66px;
        margin-left: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: 32px;
        @media (max-width: 1500px) {
            padding-right: 20px;
        }
        @media (max-width: 1115px) {
            width: 100%;
            padding-left: 15px;
            margin-left: 0;
        }
        @media (max-width: 420px) {
            margin-top: 45px;
        }
    }
    &__text-bottom {
        font-family: 'Montserrat', sans-serif;
        font-size: 16px;
        font-weight: 700;
        position: relative;
        &.active {
            color: #fff;
            &::after {
                background-color: #fff;
            }
        }
        &::after {
            content: '';
            width: 45px;
            height: 2px;
            display: inline-block;
            background-color: #2D2D2D;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            margin-left: 12px;
        }
        @media (max-width: 700px) {
            color: #fff;
            &::after {
                background-color: #fff;
            }
        }
    }
    &__icons-bottom {
        display: flex;
        column-gap: 20px;
        @media (max-width: 700px) {
            background-color: #0000007e;
            padding: 3px;
        }
        svg {
            cursor: pointer;
            &.active {
                // circle {
                //     fill: #102F28;
                // }
                path {
                    fill: #fff;
                }
            }
        }
        @media (max-width: 700px) {
            svg {
                path {
                    fill: #fff;
                }
            }
        }
    }
}

.main-text-bottom-dekst {
    @media (max-width: 940px) {
        display: none;
    }
}
.main-text-bottom-mobile {
    display: none;
    @media (max-width: 940px) {
        display: block;
    }
}

