
.credit {
    padding: 50px 0;
    position: relative;

    .dark-color {
        &.active {
            color: #fff;
        }
    }
    &__container {
        position: relative;
        z-index: 1;
    }
    &__content-wrp {
        display: flex;
        justify-content: space-between;
        @media (max-width: 1480px) {
            align-items: center;
        }
        @media (max-width: 960px) {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            margin: 0 auto;
        }
    }
    &__content-left {
        //padding-top: 110px;
        margin-right: 20px;
        @media (max-width: 1072px) {
            padding-top: 0px;
            flex: 0 1 50%;
        }
        @media (max-width: 960px) {
            margin-right: 0;
        }
        @media (max-width: 612px) {
            width: 100%;
        }
    }
    &__title {
        max-width: 585px;
        line-height: 1.1;
    }
    &__sub-title {
        max-width: 526px;
        line-height: 1.1;
        margin-top: 10px;
    }
    &__form {
        margin-top: 40px;
    }
    &__name, &__phone {
        width: 460px;
        border: solid 1px;
        display: block;
        margin-bottom: 22px;
        background-color: transparent;
        @media (max-width: 1100px) {
            width: 100%;
        }
    }
    &__btn {
        width: 460px;
        @media (max-width: 1100px) {
            width: 100%;
        }
    }
    &__btn-text {
        color: #2D2D2D;
    }

    &__content-right {
        display: flex;
        img {
            max-width: 100%;
            height: auto;
            @media (max-width: 1480px) {
                height: 100%;
            }
        }
        @media (max-width: 1720px) {
            width: 57%;
        }
        @media (max-width: 1480px) {
            height: 554px;
        }
        @media (max-width: 960px) {
            height: 430px;
            width: 585px;
            margin-top: 30px;
        }
        @media (max-width: 612px) {
            width: 100%;
        }
        @media (max-width: 520px) {
            height: 330px;
        }
        @media (max-width: 420px) {
            width: 108%;
            height: 280px;
        }
        @media (max-width: 350px) {
            height: 230px;
        }
    }
    &__img-left{
        img {
            display: block;
            @media (max-width: 1480px) {
                height: 50%;
            }
        }
    }
    &__bg {
        position: absolute;
        left: -42px;
        bottom: -290px;
    }
    .credit__slogan {
        font-family: 'Akaya Kanadaka', cursive;
        margin-top: 85px;
        display: block;
        color: #2d2d2d9c;
        letter-spacing: 2px;
        @media (max-width: 1100px) {
            margin-top: 30px;
        }
    }
}
