
.overlay1, .overlay2, .overlay3{
    opacity: 0;
    visibility: hidden;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 100%;
    background-color: #000000b4;
    padding: 50px 0;
    transition: all .3s;
    &.active {
        opacity: 1;
        visibility: visible;
    }
}
.overlay1 {
    // opacity: 0;
    // visibility: hidden;
    &.active {
        opacity: 1;
        visibility: visible;
    }
}

.form-modal-inputs {
    position: relative;
    z-index: 3;
}
.form-modal {
    width: 890px;
    margin: 0 auto;
    background-color: #fff;
    padding: 100px 15px;
    position: relative;
    overflow: hidden;
    @media (max-width: 925px) {
        width: 100%;
    }
    @media (max-width: 520px) {
        padding: 50px 15px;
    }

    input {
        border: 1px solid #2d2d2d7f;
        display: block;
        width: 460px;
        margin: 0 auto;
        margin-bottom: 22px;
        &::-webkit-input-placeholder {color:#2d2d2d7f;}
        &::-moz-placeholder          {color:#2d2d2d7f;}/* Firefox 19+ */
        &:-moz-placeholder           {color:#2d2d2d7f;}/* Firefox 18- */
        &:-ms-input-placeholder      {color:#2d2d2d7f;}
        @media (max-width: 520px) {
            width: 100%;
        }
    }
}
.modal-title {
    text-align: center;
    line-height: 1.1;
    @media (max-width: 520px) {
        text-transform: none !important;
        font-family: 'Montserrat', sans-serif;
        font-weight: 300;
        font-size: 20px !important;
    }
}
.modal-sub-title {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    text-align: center;
    margin-top: 9px;
    margin-bottom: 40px;
}
.form-modal-btn {
    width: 460px;
    display: block;
    margin: 0 auto;
    @media (max-width: 520px) {
        width: 100%;

    }
}
.btn-text-modal {
    color: #2D2D2D;
    text-align: center;
}
.modal-img {
    position: absolute;
}
.img-modal1 {
    top: -88px;
    right: -98px;
}
.img-modal2 {
    bottom: -119px;
    left: -113px;
}
.modal-cross {
    position: absolute;
    z-index: 3;
    right: 58px;
    top: 58px;
    cursor: pointer;
    @media (max-width: 755px) {
        right: 18px;
        top: 18px;
    }
    @media (max-width: 520px) {
        width: 25px;
        height: 25px;
    }
}

.modal-notification {
    padding: 50px 0;
}
///////////////////////////////////////////////////////

.overlay2-content-wrp {
    position: relative;
    z-index: 3;
}
.img-notification {
    display: block;
    margin: 0 auto;
}
.sub-title-modal {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    text-align: center;
    line-height: 1.26;
    margin: 0 auto;
    margin-top: 26px;
}
.btn-notification {
    width: 194px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #102F28;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 16px;
    margin: 0 auto;
    margin-top: 26px;
    color: #ffffff;
    transition: background .3s;

    &:hover {
        background-color: #0c231e;;
    }
}

