
.brands {
    padding: 100px 0px 50px 0px;
    position: relative;

    .dark-color {
        &.active {
            color: #fff;
        }
    }
    &__container {
        position: relative;
        z-index: 1;
    }
    &__bg {
        position: absolute;
        top: 242px;
        right: 0;
        @media (max-width: 1920px) {
            right: -95px;
        }
    }
    &__title {
        text-align: center;
        @media (max-width: 414px) {
            text-align: left;
        }
    }
    &__img-wrp {
        display: flex;
        border: solid 1px #2d2d2d4f;
        margin-top: 40px;
        @media (max-width: 1160px) {
            flex-wrap: wrap;
            border: none;
        }
        @media (max-width: 620px) {
            border: none;
            justify-content: center;
        }
        @media (max-width: 414px) {
            margin-top: 32px;
        }
    }
    &__img-item {
        flex: 0 1 251px;
        height: 250px;
        display: flex;
        justify-content: center;
        align-items: center;
        &:not(:last-child) {
            border-right: solid 1px #2d2d2d4f;
        }
        &:nth-child(even){
            background-color: #000000;
        }
        @media (max-width: 1500px) {
            padding: 10px 10px;
        }
        @media (max-width: 1160px) {
            flex: 0 0 33.33%;
            border: solid 1px #2d2d2d4f;
        }
        @media (max-width: 850px) {
            flex: 0 0 50%;
        }
        @media (max-width: 620px) {
            flex: 0 0 415px;
        }
        @media (max-width: 438px) {
            flex: 0 1 100%;
        }
    }
    &__img {
        @media (max-width: 1500px) {
            width: 100%;
        }
        @media (max-width: 1160px) {
            width: 88%;
            max-height: 98%;
        }
    .dark-color {
        &.active {
            background-color: #16161683;
            &:nth-child(even) {
                background-color: #000;
            }
        }
    }
}

}