
.catalog {
    padding: 50px 0px;
    overflow: hidden;
    @media (max-width: 1260px) {
        padding-top: 0;
    }

    &__title {
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 40px;
    }
    .dark-color {
        &.active {
            color: #fff;
        }
    }
}

 .swiper-wrp {
     position: relative;
     padding: 0 20px;
     width: 90%;
     margin: 0 auto;
     @media (min-width: 1800px) {
        width: 100%;
    }
    @media (max-width: 1260px) {
        width: 100%;
        padding: 0;
    }
 }
.swiper {
    width: 100%;
    height: 600px;
    @media (max-width: 1260px) {
        height: 400px;
    }
    @media (max-width: 400px) {
        width: 110%;
        transform: translateX(-15px);
    }
    @media (max-width: 660px) {
        height: 340px;
    }
}
.swiper-slide {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 10px;
    img {
        width: 100%;
        height: 100%;
        position: absolute;
    }
    span {
        position: relative;
        color: #fff;
        text-align: center;
        display: block;
        z-index: 1;
    }
}
.title-slide {
   font-size: 40px;
   line-height: 1.3;
   @media (max-width: 1600px) {
       font-size: 32px;
   }
   @media (max-width: 1200px) {
       font-size: 22px;
   }
   @media (max-width: 660px) {
       font-size: 18px;
   }
}
.swiper-button-prev, .swiper-button-next {
    top: 50%;
    transform: translateY(-50%);
    &::after {
        display: none;
    }
    @media (max-width: 1260px) {
        display: none;
    }
}
.swiper-button-prev {
    left: -40px;
    @media (min-width: 1700px) {
        left: -64px;
    }
}
.swiper-button-next {
    right: -40px;
    @media (min-width: 1700px) {
        right: -64px;
    }
}